.products{
    min-height: calc(100vh - 90px); padding-top: 90px;
}

.products_wrap{
    padding: 5% 3%;
    text-align: center;
}

#products_title{
    margin-bottom: 50px;
    h1{font-size: 2.0rem;}
    hr{border: none; border-bottom: 5px solid #212121; width: 150px; margin: 2px auto;}
    h2{margin-top: 20px; font-size: 1.0rem; font-weight: 400;}
}
#products_h2{
    color: white;
}
#products_search{
    margin-top: 80px;
    position: relative;
    display: flex; align-items: center; justify-content: center;
    input{border: 1px solid #212121; border-radius: 50px; width: 40%; height: 50px; padding: 0 30px;}
    input:focus{outline:none;}
}
#products_search_icon{
    position: absolute; right: 30%; top: 15px;
    display: flex; gap: 12px;
    img{width: 20px; cursor: pointer;}
}
#product_search_keyword{
    margin: 20px 0;
    display: flex; justify-content: center; gap: 10px;
    button{
        border: none; border-radius: 20px;
        padding: 5px 15px;
    }
}

.products_item{
    width: 100%; max-width: 1440px; margin: 0 auto;
    display: flex; justify-content: left; gap: 20px;
    flex-wrap: wrap;
}
.gallery_wrap{
    box-sizing: border-box;
    width: calc(20% - 20px)
}
.gallery{
    border-radius: 10px; box-sizing: border-box; box-shadow: 2px 2px 5px lightgray;
    margin-bottom: 20px; padding: 20px;
    position: relative; text-align: center;
}
.gallery .g_img{
    width: 100%; aspect-ratio: 1 / 1; margin: 0 auto;
    overflow: hidden;
    img{width: 100%; transition: all 0.5s ease;}
    img:hover{scale: 1.1;}
}
.gallery .txt1{
    margin: 20px 0 10px 0;
}
.gallery .txt2{
    margin: 10px 0;
    font-size: 14px;
}
.empty{
    margin: 0 auto;
}
