/* media 반응형 스타일 */
@media screen and (min-width:1280px){
    #m4_top_div1 ul li:nth-child(2){width: 75%;}
}
@media screen and (max-width:1280px){
    #m4_top_div1 ul li:nth-child(3){display: none;}
    #m4_top_div1 ul li:nth-child(2){width: 90%;}
    #m3_list span{font-size: 0.9rem;}
}
@media screen and (max-width:1024px){
    #m4_top_div1 ul li:nth-child(1){display: none;}
    #m4_top_div1 ul li:nth-child(2){width: 100%; font-size: 0.9rem; padding-left: 10px;}
}
@media screen and (min-width:1025px){
    #m3_list span{display: inline-block;}
    .m3_txt2{display: inline-block;}
}
@media screen and (max-width:1024px){
    #m3_list span{display: none;}
    .m3_txt2{display: none;}
}

.mainpage{
    padding-top: 80px;
    section{width: 100%;}
}

.btn_ho{
    border: 1px solid #212121; border-radius: 30px;
    padding: 3px 10px;
    transition: all 0.5s ease;
}
.btn_ho:hover{background-color: #33ff33;}

/* 섹션1 - 메인배너 */
.m_sec_1{
    width: 100%;
    position: relative;
    img{height: 100%; overflow: hidden;}
}
.swiper{
  width: 100%; height: 100%;
}
.swiper-slide {
  display: flex; justify-content: center; align-items: center;
}
.swiper-slide img{
  width: 100%; height: 100%; object-fit: cover;
}
#m1_text{
    width: 100%;
    position: absolute; z-index:998; left: 0; top: 0;
}
#m1_text img{
    width: 300px; padding-top: 11%; padding-left: 20%;
}

/* 섹션2 - 검색영역 */
.m_sec_2{
    width: 100%; padding: 5% 0;
}

/* 섹션3 - 상품영역 */
.m_sec_3{
    background-color: #fdfadd;
    width: 100%; padding: 7% 0;
    position: relative;
    display: flex;
}
.m3_wrap{
    width: 1440px; margin: 0 auto; padding: 0 3%;
    display: flex;
}
#m3_list{
    width: 30%;
    ul{list-style: none; margin: 0; padding: 0;}
    li{margin-bottom: 42px;}
    li a{padding-bottom: 3px; color: gray;}
    li:hover a{border-bottom: 5px solid #33ff33; color: #212121; font-weight: bold; transition: all 0.2s linear;}
    span{margin-left: 40px; opacity: 0; transition: all 0.3s linear;}
    li:hover span{opacity: 1;} 
}
#m3_list ul li:nth-child(1){
    a{border-bottom: 5px solid #33ff33; color: #212121; font-weight: bold;}
    span{opacity: 1;}
}
#m3_product{
    width: 70%; margin: 0; padding: 0;
    position: relative;
}
#m3_icon{
    img{width: 25px; position: absolute; right: 0; bottom: -20px; transition: all 0.3s linear;}
}
#m3_icon:hover{
    #m3_i2{opacity: 0;}
}
#m3_product_top, #m3_product_bottom{
    display: flex; justify-content: right;
    div{width: 33%; margin-left: 2%; padding-bottom: 1.5%;}
    div img{border: 1px solid #212121; box-sizing: border-box; width: 100%;}
}
.m3_gallery{
    position: relative; text-align: center;
}
.m3_txt1{
    background-color: white; border: 1px solid #212121;
    width: 125px; margin: 0 auto; padding: 1px 0;
    position: absolute; bottom: 60px; left: 50%; transform: translate(-50%, 0%);
    display: none;
}
.m3_gallery:hover .m3_txt1{
    display: block;
}
.m3_txt2{
    background-color: #fdfadd; border: 1px solid #212121;
    width: 100px; margin: 0 auto; padding: 1px 0;
    font-weight: bold;
    position: absolute; bottom: 30px; left: 50%; transform: translate(-50%, 0%);
    display: none;
}
.m3_gallery:hover .m3_txt2{
    display: block;
}

/* 섹션4 - 공지영역 */
.m_sec_4{
    width: 100%; padding: 5% 0;
    display: flex;
}
.m4_wrap{
    width: 1440px; margin: 0 auto; padding: 0 3%;
}
.m4_top, .m4_bottom{
    width: 100%;
    display: flex; align-items: center; justify-content: space-between;
}
#m4_top_div1{
    width: 49%; height: 200px; margin-bottom: 2%;
    div:nth-child(1){
        background-color: #fdfadd;
        margin-bottom: 10px;
        display: flex; align-items: center; justify-content: space-between;
        h2 a{padding-left: 10px;}
        h3 a{padding-right: 10px;}
    }
    ul{
        border-bottom: 1px solid lightgray;
        width: 100%; height: 20px; margin: 0; padding: 8px 0 5px 0;
        list-style: none;
        display: flex; justify-content: space-between;
    }
    ul:nth-child(1){border-top: 1px solid lightgray;}
    ul li:nth-child(1){width: 10%; padding-left: 10px;}
    ul li:nth-child(3){width: 15%; padding-right: 10px; text-align: right; font-family: 'CloudSansCode';}
    ul:hover{background-color: #fdfadd;}
    ul:hover a{font-weight: bold;}
}
#m4_top_div2{
    width: 49%; height: 200px; margin-bottom: 2%;
    display: flex; align-items: center; justify-content: right;
    > div{
        border: 1px solid lightgray; border-radius: 10px;
        width: 90%; height: 170px; text-align: center;
        display: flex; align-items: center; justify-content: center;
        h2{margin-bottom: 20px; padding-left: 10px;}
        p{font-size: 1.5rem; color: #212121;}
    }
}
.m4_bottom_bnn{
    border-radius: 10px; box-sizing: border-box; box-shadow: 2px 2px 5px lightgray;
    width: 32%;
    overflow: hidden;
    a{display: flex; align-items: center;}
    a img{width: 100%;}
}
