.signup{
    height: calc(100vh - 90px); padding-top: 90px;
}

.signup .contents_wrap{
    height: 100%;
    display: flex; flex-direction: column; justify-content: center;
}

.signup .signup_rel{
    position: relative;
}
.signup .signup_abs{
    background-color: black; border: none;
    width: 70px; height: 32px;
    position: absolute; right: 0; top: 0;
    a{color: white; transition: all 0.3s linear;}
    a:hover{color: #33ff33;}
}
#signup_in{
    width: 480px; height: max-content; margin: 0 auto;
    text-align: center;
    h1{margin-bottom: 50px;}
    > button{background-color: black; border: none; width: 100%; height: 35px; margin-top: 5px;}
    > button a{padding: 10px 200px; color: white; font-size: 0.9rem; font-weight: bold;  transition: all 0.3s linear;}
    > button:hover a{color: #33ff33;}
}
#signup_in label{
    display: flex; align-items: center; justify-content: center; gap: 10px;
    input{accent-color: #33ff33;}
    p{font-size: 0.8rem; display: flex; align-items: center; gap: 5px;}
    a{font-size: 0.8rem; background-color: #33ff33;}
    a:hover{font-weight: bold;}
}
#signup_in ul{
    list-style: none; margin: 5px auto; padding: 0;
    display: flex; align-items: center; justify-content: space-between;
    font-size: 0.9rem;
    span{color: red;}
}
#signup_in ul input{
    width: 258px; height: 30px;
    border: 1px solid #212121; border-radius: 0; padding: 0 10px;
}
#signup_in ul input:focus{
    outline:none;
}


.signup .login_wrap{
    margin-top: 50px;
    display: flex; align-items: center; justify-content: center;
}
#login_in{
    width: 480px; height: 17px;
    display: flex; align-items: bottom; justify-content: space-between;
    p{margin: 0; font-size: 0.9rem;}
    a{border-bottom: 1px solid #212121; padding-bottom: 2px; font-size: 0.9rem; font-weight: bold;}
    a:hover{border-bottom: 10px solid #33ff33; font-weight: bold;}
}
