.login{
    height: calc(100vh - 90px); padding-top: 90px;
}

.login .contents_wrap{
    height: 100%;
    display: flex; flex-direction: column; justify-content: center;
}

.login .login_wrap{
    display: flex; align-items: center; justify-content: center;
}
#login{
    width: 480px; margin: 0 auto; text-align: center;
    h1{margin-bottom: 50px;}
    button{
        border: none; background-color: black;
        width: 100%; height: 35px; margin-top: 5px;
        color: white; font-size: 0.9rem; font-weight: bold;
        transition: all 0.3s ease; cursor: pointer;}
    button:hover{color: #33ff33;}
}
#login label{
    margin-left: 8px;
    display: flex; align-items: center; justify-content: center; gap: 5px;
    input{accent-color: #33ff33;}
    p{font-size: 0.8rem;}
}
#login ul{
    margin: 5px auto; padding: 0;
    list-style: none; font-size: 0.9rem;
    display: flex; align-items: center; justify-content: space-between;
}
#login ul input{
    border: 1px solid #212121; border-radius: 0;
    width: 258px; height: 30px; padding: 0 10px;
}
#login ul input:focus{
    outline:none;
}

.login .find_wrap{
    margin-top: 50px;
    display: flex; align-items: center; justify-content: center;
}
#find{
    width: 480px; height: 17px;
    display: flex; align-items: bottom; justify-content: space-between;
    p{margin: 0; font-size: 0.9rem;}
    a{border-bottom: 1px solid #212121; padding-bottom: 2px; font-size: 0.9rem; font-weight: bold;}
    a:hover{border-bottom: 10px solid #33ff33; font-weight: bold;}
}

.login .signup_wrap{
    margin-top: 15px;
    display: flex; align-items: center; justify-content: center;
}
#signup{
    width: 480px; height: 17px;
    display: flex; align-items: bottom; justify-content: space-between;
    p{margin: 0; font-size: 0.9rem;}
    a{border-bottom: 1px solid #212121; padding-bottom: 2px; font-size: 0.9rem; font-weight: bold; }
    a:hover{border-bottom: 10px solid #33ff33; font-weight: bold;}
}
