/* footer 하단 스타일 */
footer{
    position: relative;
}
.f_wrap{
    width: 100%; height: 180px; background-color: black; color: white;
    position: absolute; top: 0; left: 0; margin: 0; padding: 0;
}
#f_left{
    max-width: 1440px; height: 100%; margin: 0 auto; padding: 0 3%;
    line-height: 160%;
    display: flex; align-items: center; justify-content: space-between;
}
